@import url(https://fonts.googleapis.com/css?family=Dancing+Script|Work+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
	font-size: 62.5%;
}

body {
	background-color: #120003;
	min-height: 175vh;
}

header {
	height: 80vh;
	margin: 0 0 0 100px;
}

h1 {
	font-family: 'Dancing Script', cursive;
	font-size: 9rem;
	margin: 0;
	padding: 150px 0 0 0;
	color: #EEE8AA;
}

h2 {
	margin: 0;
	padding: 50px 0 0 0;

	font-family: 'Work Sans', sans-serif;
	font-size: 2rem;
	line-height: 4rem;
	color: #EEE8AA;
}

main {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-flow: row nowrap;
	        flex-flow: row nowrap;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
}

.search {
	width: 30%;
	margin: 0 100px 0 0;
}

input {
	margin: 0 0 5px 100px;
	padding: 0 10px;
	width: 322px;
	height: 50px;

	background-color: #120003;
	font-family: 'Work Sans', sans-serif;
	border: 1px solid #EEE8AA;
	color: #EEE8AA;
}

input:focus {
	outline: 3px solid #EEE8AA;
}

.moviePanel.flexContainer {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-flow: row nowrap;
	        flex-flow: row nowrap;
	-webkit-justify-content: space-between;
	        justify-content: space-between;

	margin: 0 0 5px 100px;
	padding: 10px;
	width: 300px;

	background-color: #120003;
	font-family: 'Work Sans', sans-serif;
	border: 1px solid #EEE8AA;
	color: #EEE8AA;
	text-align: right;
	font-size: 1.2rem;
}

.suggestions .moviePanel:hover {
	background-color: #322023;
	cursor: pointer;
}

img,.imagePlaceholder {
	min-width: 40px;
	height: 90px;
	object-fit: contain;
}

.moviePanel .details {
	width: 75%;
	padding: 10px 20px 0 0;
}

.close {
	color: #EEE8AA;
	width: 10px;
}

.close:hover {
	cursor: pointer;
}

.submit:hover {
	cursor: pointer;
	background-color: #322023;
}

.resultBackground {
	position: fixed;
	z-index: 1;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,0.4);
}

.resultBox {
	position: relative;
	width: 40%;
	color: #EEE8AA;
	background-color: #221013;
	border: 1px solid #EEE8AA;
	margin: 20% auto;
	text-align: center;
}

.resultHeader {
	font-family: 'Work Sans', sans-serif;
	font-size: 2rem;
}

.result {
	font-family: 'Dancing Script', cursive;
	font-size: 2rem;
}

.resultBox .close {
	position: absolute;
	top: 10px;
	right: 10px;
}

footer {
	margin: 0 0 0 100px;

	font-size: 1.2rem;
	font-family: 'Work Sans', sans-serif;
	text-align: left;
	color: #EEE8AA;
}

